import { Player } from '../player';

export class Game {

    first: Player;
    second: Player;
    results: Result[] = [];

    pointsByFirst : number = 0;
    pointsBySecond : number = 0;

    constructor(first: Player, second: Player) {
        this.first = first;
        this.second = second;

        for (let i = 0; i < 10; i++) {
            if (first.submission.points[i] > second.submission.points[i]) { this.results.push(Result.FIRST); this.pointsByFirst += ( i + 1) }
            if (first.submission.points[i] == second.submission.points[i]) { this.results.push(Result.DRAW) }
            if (first.submission.points[i] < second.submission.points[i]) { this.results.push(Result.SECOND) ; this.pointsBySecond += ( i + 1)}
        }

    }

}


export enum Result {
    FIRST,
    DRAW,
    SECOND
}