import { Component } from '@angular/core';
import { TournamentService } from '../tournament.service';
import { Submission } from 'src/app/submission/submission';
import { registerPlayer } from 'src/app/player';

@Component({
  selector: 'bulk-input',
  templateUrl: './bulk-input.component.html'
})
export class BulkInputComponent {
  tournamentService: TournamentService;

  constructor(tournamentService: TournamentService) {
    this.tournamentService = tournamentService;
  }

  jsonInput : string = "[{ \"points\": [ 1,2,3,4,5,6,7,8,9,55 ]}]";

  addAll() {
    let bulkInput : Submission[] = JSON.parse(this.jsonInput);
    bulkInput.forEach(submission => this.tournamentService.addPlayer(registerPlayer(submission)))
  }

}
