import { Component } from '@angular/core';
import { TournamentService } from '../tournament.service';
import { buildSubmission, Submission } from 'src/app/submission/submission';
import { buildPlayer, registerPlayer } from 'src/app/player';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'tournament-entry',
  templateUrl: './entry.component.html',
  styleUrls: ['./entry.component.css']
})
export class EntryComponent {
  tournamentService: TournamentService;

  constructor(tournamentService: TournamentService) {
    this.tournamentService = tournamentService;
  }

  first = new FormControl(0);
  second = new FormControl(0);
  third = new FormControl(0);
  fourth = new FormControl(0);
  fifth = new FormControl(0);
  sixth = new FormControl(0);
  seventh = new FormControl(0);
  eighth = new FormControl(0);
  nineth = new FormControl(0);
  tenth = new FormControl(0);

  total: number = 0;
  remaining: number = 100;
  isValid: boolean = false


  addPlayer() {
    let additionalPlayer = registerPlayer(this.submissionFromForm())
    this.tournamentService.addPlayer(additionalPlayer);
    this.resetForm();
  }
  resetForm() {
    this.first.setValue(0); this.second.setValue(0); this.third.setValue(0); this.fourth.setValue(0); this.fifth.setValue(0);
    this.sixth.setValue(0); this.seventh.setValue(0); this.eighth.setValue(0); this.nineth.setValue(0); this.tenth.setValue(0);

    this.updateTotal();
  }

  updateTotal() {
    let submission = this.submissionFromForm()
    this.total = submission.sum();
    this.remaining = 100 - this.total;
    this.isValid = submission.isComplete();

  }

  private submissionFromForm(): Submission {
    return buildSubmission(this.first.value, this.second.value, this.third.value, this.fourth.value, this.fifth.value,
      this.sixth.value, this.seventh.value, this.eighth.value, this.nineth.value, this.tenth.value)
  }

}
