import * as uuid from 'uuid';
import { Submission, buildSubmission } from './submission/submission';

export interface Player {
    id: string,
    submission: Submission
}

export function registerPlayer(submission: Submission) {
    return buildPlayer(uuid.v4(), submission)
}
export function buildPlayer(idForPlayer: string, submission: Submission) {
    return {
        'id': idForPlayer,
        'submission': submission
    };
}

export function defaultPlayer() {
    return buildPlayer(DEFAULT_PLAYER_ID, buildSubmission(10, 10, 10, 10, 10, 10, 10, 10, 10, 10))
}

export const DEFAULT_PLAYER_ID = '10x10'